@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;700&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Geologica:wght@100&display=swap');
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: "Geologica", sans-serif;
}

:root {
	--mainColor: #ffffff;
	--mainColorLight: #5767aa;
	--secondaryColor: #db2b39;
	--textColor: #222222;
}
.cnt{
	
	
}
.top{
	height: 60px;
	background-color: black;
	margin-bottom: 25px;
}
.shopCart{
	
	padding: 5px;
	margin-left: 10px;
	border: 2px solid;
	border-color: transparent;
	background-color:#4819BB;
	width: 90px;
	height: 40px;
	border-radius: 5px;
	display: flex;
	justify-content: center;
	align-items: center;
}
.cartCount{
	margin-left: 10px;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #5C25E2;

	border: 2px solid;
	border-color: transparent;

	height: 30px;
	width: 30px;
	border-radius: 45px;
}
.cats{
	font-weight: bold;

}
.ics{
	object-fit: contain;
	width: 20px;
	position: relative;
	top: 25px;
	right: 73px;
}
.gpu{
	width: 27px;
	right: 76px;
}
img{
	
}
.shopCart h3{
	font-size: 17px;
	margin-bottom: 1px;
	color: rgb(253, 253, 253);
}
.cont{
	
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-left: 350px;
	margin-right: 350px;
}


header {
	border-top: solid rgb(233, 233, 233);
	border-bottom: solid rgb(233, 233, 233);
	border-width: 1px;
	margin-top: 20px;
	margin-bottom: 0px;
	display: flex;
	
	justify-content: center;
	height: 40px;
	padding: 0 2rem;
	background-color:rgb(255, 255, 255);
	color: var(--textColor);
}

nav a {
	margin: 0 1rem;
	color: var(--textColor);
	text-decoration: none;
	
}

nav a:hover {
	color: var(--secondaryColor);
}

header .nav-btn {
	padding: 5px;
	cursor: pointer;
	background: transparent;
	border: none;
	outline: none;
	color: var(--textColor);
	visibility: hidden;
	opacity: 0;
	font-size: 1.8rem;
}

header div,
nav {
	display: flex;
	align-items: center;
}
header .nav-btn {
	display: none;
}
.logo{
	width: 200px;
	height: 200px;
}
@media only screen and (max-width: 1024px) {
	.logo{
		margin-left: 50px;
	}
	header .nav-btn {
		display: block;
		visibility: visible;
		opacity: 1;
	}
	header{
		margin-left: 0px;
	}

	header nav {
		position: fixed;
		top: -100vh;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 1.5rem;
		background-color: var(--mainColor);
		transition: 1s;
	}

	header .responsive_nav {
		transform: translateY(100vh);
	}

	nav .nav-close-btn {
		position: absolute;
		top: 2rem;
		right: 2rem;
	}

	nav a {
		font-size: 1.5rem;
	}
}